    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 9999;
    display: none;
  }
  
  .real_bgc {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    position: relative;
  }
  
  .real_wrap {
    width: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    img {
      width: 100%;
      height: auto;
    }
  }
  
  .size_close {
    font-size: 50px;
    position: absolute;
    top: 150px;
    right: 300px;
    color: #fff;
    cursor: pointer;
  }
  
  /*********답글**********/
  
  .re_pop {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 99999;
    display: none;
  }
  
  .re_bgc {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
