  /*********footer********/
  
  footer {
    width: 100%;
    text-align: center;
    margin-top: 100px;
  }
  
  .footer_wrap {
    border-top: 1px solid #ccc;
    background-color: #eee;
    padding: 20px 10px;
  
    .footer_box1 {
      display: flex;
  
      .foot_logo {
        width: 20%;
  
        img {
          width: 90px;
          height: 30px;
        }
